import React from 'react';
import { Icon, Button } from 'antd';
import BaseContainer from '../../Core/BaseContainer';
import DropContainer from '../DropContainer';
import BoxEdit from '../../EditDrawer/BoxEdit';
import './index.css';
import { BOX_CONTINAER } from '../../../utils/Constants';

class BoxContainer extends BaseContainer {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
            configs: {
                cellsArr: [{
                    layoutIndex: 0,
                }],
                layoutColumn: 1, // 
            },
        };
        this.onAddRowCell = this.onAddRowCell.bind(this);
        this.onReceiveConfigs = this.onReceiveConfigs.bind(this);
        this.onUpdateConfigs = this.onUpdateConfigs.bind(this);
        this.onDeleteContainer = this.onDeleteContainer.bind(this);
        this.onToggleEditDrawer = this.onToggleEditDrawer.bind(this);
    }
    onReceiveConfigs({ deleteFlag = false, ...values }) {
        const { configs } = this.state;
        const { layoutColumn } = this.props;
        const newConfigs = Object.assign({ ...configs }, layoutColumn);
        const newCellsArr = newConfigs.cellsArr.reduce((arr, item) => {
            // 
            if (deleteFlag) {
                if (values.layoutIndex !== item.layoutIndex) {
                    arr.push(Object.assign({}, item));
                } else {
                    arr.push({
                        layoutIndex: values.layoutIndex,
                    });
                }
            } else {
                if (values.layoutIndex === item.layoutIndex) {
                    arr.push(Object.assign({}, item, values));
                } else {
                    arr.push(Object.assign({}, item));
                }
            }
            return arr;
        }, []);
        newConfigs.cellsArr = newCellsArr;
        this.onUpdateConfigs(newConfigs);
    }
    onAddRowCell() {
        const { onUpdateLayoutConfig, layoutIndex } = this.props;
        const { configs } = this.state;
        const { cellsArr, layoutColumn } = configs;
        const newCellsArr = cellsArr.concat([{
            layoutIndex: cellsArr.length,
        }]);
        const newConfigs = {
            layoutColumn,
            cellsArr: newCellsArr,
        };
        const newState = {
            configs: newConfigs,
        };
        this.setState({
            ...newState,
        }, () => {
            onUpdateLayoutConfig({ layoutIndex, configs: newConfigs });
        });
    }
    rendeCell(layoutColumn) {
        if (!layoutColumn) {
            return null;
        }
        const { preview, context = this, apiData, execFetchApi, onShowLoginModal, ...restProps } = this.props;
        const componentArr = [];
        const width = `${100 / layoutColumn}%`;
        const { configs } = preview ? this.props : this.state;
        const { cellsArr } = configs;
        const cellNum = cellsArr.length;
        const { layerConfig } = this.props;
        const previewProps = {
            preview,
        };
        const limitIndex = Math.max(cellNum, layoutColumn);
        for (let i = 0; i < limitIndex; i++) {
            const layoutIndex = i;
            previewProps.configs = Object.assign(cellsArr.length - 1 >= i ? cellsArr[i] : {}, { layoutIndex });
            componentArr.push((
               <div className="cell border-box" key={`cell-${layoutColumn}-${i}`} style={{ width }}>
                    <DropContainer
                        layoutIndex={layoutIndex}
                        apiData={apiData}
                        execFetchApi={execFetchApi}
                        layerConfig={layerConfig}
                        {...restProps}
                        {...previewProps}
                        onShowLoginModal={onShowLoginModal}
                        context={context}
                        onUpdateLayoutConfig={this.onReceiveConfigs}
                    />
               </div>
            ));
        }
        return componentArr;
    }
    _render() {
        const { visible } = this.state;
        const { onUpdateLayoutConfig, configs = {}, preview } = this.props;
        const { layoutColumn = 1, boxStyles } = configs;
        return (
            <div className={`fulu-cell-box ${ preview ? 'preview-mode' : 'edit-mode'}`} style={boxStyles}>
                <div className="clear-box">
                    {this.rendeCell(+layoutColumn)}
                </div>
                <BoxEdit
                    visible={visible}
                    configs={configs}
                    onClose={(pConfigs) => {
                        if (configs) {
                            const { layoutColumn } = pConfigs;
                            const { cellsArr } = this.state.configs;
                            const newConfigs = {
                                cellsArr,
                                layoutColumn: +layoutColumn,
                                layoutIndex: configs.layoutIndex,
                                type: BOX_CONTINAER,
                                boxStyles: pConfigs.boxStyles,
                            };
                            this.setState({
                                configs: newConfigs,
                            });
                            onUpdateLayoutConfig(newConfigs);
                        }
                        this.onToggleEditDrawer();
                    }}
                    onDeleteContainer={this.onDeleteContainer}
                />
                {
                    !preview ? (
                        <Icon type="form" className="icon-operation" onClick={this.onToggleEditDrawer} />
                    ) : null
                }
                {
                    !preview ? (
                        <div className="btn-add-box">
                            <Button
                                ghost
                                type="primary"
                                onClick={this.onAddRowCell}
                            >
                                + boxItem
                            </Button>
                        </div>
                    ) : null
                }
            </div>
        );
    }
}

export default BoxContainer;